import React from "react";

const context = [
  {
    title: " ",
    text: "          -    ,    !",
  },
  {
    title: "  / ",
    text: "       ?   !    ?    .",
  },
  {
    title: " ",
    text: "     ,    ,      -    .",
  },
  {
    title: " ",
    text: "         ?        .",
  },
  {
    title: "  ",
    text: "         ?             .",
  },
  {
    title: " ",
    text: "    ?            .",
  },
  {
    title: "  ",
    text: "  ,      ?  ,   - !",
  },
  {
    title: " ",
    text: "    ?        .",
  },
  {
    title: " ",
    text: "             ?  , .",
  },
];

export default function Services() {
  const services = [];

  context.forEach((e) => {
    services.push(
      <div class="col l4">
        <div class="card teal white-text">
          <div class="card-content">
            <h5 class="">{e.title}</h5>
            <p class="service-text">{e.text}</p>
          </div>
          <div class="card-action center-align">
            <a class="">  </a>
          </div>
        </div>
      </div>
    );
  });

  return (
    <section class="" id="services">
      <div class="container">
        <h2 class="title center-align"> </h2>
        <p class="subtitle center-align">
                   
        </p>
        <div className="row">
            { services }
        </div>
        
      </div>
    </section>
  );
}



